var snackBar = document.getElementById('snack-bar');
var snackBarMessageContainer = document.getElementById('snack-bar-text');
export var snackBarHandler = function (message, displayDuration) {
    if (!snackBar || !snackBarMessageContainer) {
        return;
    }
    snackBarMessageContainer.textContent = message;
    snackBar.classList.add('snack-bar--active');
    setTimeout(function () {
        snackBar.classList.remove('snack-bar--active');
    }, displayDuration);
};
