export var warningElementHandler = function (params) {
    var message = params.message, containerElement = params.containerElement, tagName = params.tagName, tagClassName = params.tagClassName, textTagClassName = params.textTagClassName;
    if (!containerElement) {
        return;
    }
    containerElement.insertAdjacentHTML('afterbegin', "<".concat(tagName.toLocaleLowerCase(), " class=").concat(tagClassName, ">\n      <span class=").concat(textTagClassName, ">").concat(message, "</span>\n    </").concat(tagName.toLocaleLowerCase(), ">"));
};
