import { getDataFromLocalStorage, setDataToLocalStorage, } from '../api/localStorage';
import { LocalStorageKeys, ThemeTypes, ThemeElementAttribute, } from '../types';
export var getCurrentThemeType = function () {
    var dataFromLocalStorage = getDataFromLocalStorage(LocalStorageKeys.MAIN);
    return dataFromLocalStorage.themeType;
};
export var setInitialThemeTypeOnElement = function (element) {
    if (!element) {
        return;
    }
    var currentThemeType = getCurrentThemeType();
    if (element.getAttribute(ThemeElementAttribute.NAME) === currentThemeType) {
        return;
    }
    document.body.setAttribute(ThemeElementAttribute.NAME, currentThemeType);
};
export var changeThemeType = function (currentThemeType, element) {
    var newThemeType = currentThemeType === ThemeTypes.LIGHT ? ThemeTypes.DARK : ThemeTypes.LIGHT;
    element.setAttribute(ThemeElementAttribute.NAME, newThemeType);
    setDataToLocalStorage(LocalStorageKeys.MAIN, 'themeType', newThemeType);
};
