var availableCurrenciesListElement = document.getElementById('available-currencies-list');
export var renderAddCurrencyList = function (currenciesData, currenciesList) {
    if (!availableCurrenciesListElement) {
        return;
    }
    var availableCurrenciesListData = currenciesData.filter(function (currency) { return !currenciesList.has(currency.Cur_Abbreviation); });
    availableCurrenciesListElement.innerText = '';
    availableCurrenciesListData.forEach(function (currency) {
        availableCurrenciesListElement.insertAdjacentHTML('beforeend', "<div class=\"add-currencies__checkbox-wrapper\">\n        <input class=\"add-currencies__input\" type=\"checkbox\" id=".concat(currency.Cur_ID, "-").concat(currency.Cur_Abbreviation, " name=").concat(currency.Cur_Abbreviation, " data-add-currencies-checkbox>\n        <label class=\"add-currencies__label\" id=\"add-currencies-label\" for=").concat(currency.Cur_ID, "-").concat(currency.Cur_Abbreviation, " data-currency-abbreviation=").concat(currency.Cur_Abbreviation, ">").concat(currency.Cur_Name_Eng, " (").concat(currency.Cur_Abbreviation, ")</label>\n      </div>"));
    });
};
